.typeprac-container {
  min-height: calc(100vh - 160px);
  background-color: white;
  padding: 40px;
  display: flex;
  justify-content: center;
  font-family: 'Montserrat'; }
  .typeprac-container .header {
    display: flex;
    justify-content: space-between; }
  .typeprac-container .question-container {
    border: 1px solid;
    width: 500px;
    max-width: 80vw;
    font-size: 20px;
    margin: 16px 0;
    padding: 0 16px;
    -webkit-user-select: none;
    -webkit-touch-callout: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
    .typeprac-container .question-container .curr {
      background-color: #e0f7ff;
      padding: 3px;
      border-radius: 3px; }
  .typeprac-container .txt-ques {
    margin-right: 8px;
    line-height: 2; }
  .typeprac-container .songs {
    margin-bottom: 8px; }
    .typeprac-container .songs .txt-song-name {
      text-align: left;
      width: 170px;
      text-overflow: ellipsis;
      overflow-x: hidden; }
  .typeprac-container .txt-input {
    max-width: 89vw; }
  .typeprac-container .wpm {
    background-color: #e0ffea;
    padding: 3px;
    border-radius: 3px; }

.img-idol {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 18px;
  cursor: pointer; }
  .img-idol:hover {
    opacity: 0.7; }

.modal-container {
  display: flex;
  flex-wrap: wrap; }
