.blog-container {
  min-height: calc(100vh - 160px);
  background-color: #ebebeb;
  display: flex;
  justify-content: center;
  font-family: 'Montserrat';
  font-size: 3vw;
  padding: 20px 0 30px; }
  .blog-container .container {
    max-width: 1070px;
    width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto; }
    .blog-container .container .blog-container-left .header {
      background: linear-gradient(#a9f4ff, transparent), linear-gradient(to top left, #ffe0a7, transparent), linear-gradient(to top right, #0051ff, transparent);
      background-blend-mode: screen;
      padding: 5px 20px;
      color: #fff; }
    .blog-container .container .blog-container-left .content {
      padding: 5px 20px;
      background-color: #fff; }
      .blog-container .container .blog-container-left .content .url {
        cursor: pointer; }
    .blog-container .container .blog-container-right {
      margin-left: 30px;
      padding: 65px 75px 50px; }
      .blog-container .container .blog-container-right .article-container {
        display: flex;
        border-bottom: 1px solid #fff;
        padding-bottom: 24px; }
        .blog-container .container .blog-container-right .article-container .detail {
          width: 100%; }
          .blog-container .container .blog-container-right .article-container .detail .title {
            font-size: 1.375rem;
            color: #1d364a; }
          .blog-container .container .blog-container-right .article-container .detail .description {
            font-size: 1rem;
            letter-spacing: 2px;
            line-height: 1.3; }
        .blog-container .container .blog-container-right .article-container .image-like-div {
          display: inline-flex;
          background-color: #fff;
          border-radius: 4px;
          border: #c3d9e1 5px solid;
          width: 200px;
          height: 100px;
          margin: 12px 0 12px 12px;
          padding: 12px; }
