.App-header {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white; }
  .App-header h1 {
    margin-left: 40px;
    font-family: 'Montserrat';
    font-size: 12px;
    letter-spacing: 5px; }
  .App-header .menu {
    display: flex;
    font-size: 12px;
    margin-right: 40px; }
    .App-header .menu .menu-item {
      margin: 0 16px;
      cursor: pointer; }
      .App-header .menu .menu-item:hover {
        text-decoration: underline; }
      .App-header .menu .menu-item.active {
        font-weight: 600; }

.App-link {
  color: #61dafb; }

body {
  background: #000;
  min-height: calc(100vh - 160px);
  color: #6f6f6f;
  font-family: 'Lato', sans-serif;
  font-size: 12px;
  position: relative; }

footer {
  background: #000;
  font-size: 14px;
  color: #ddd;
  padding: 40px;
  text-align: left; }
